import React, { Component } from "react";
import "./App.css";
import TitleSection from "./sections/TitlesSection";
import AboutSection from "./sections/AboutSection";
import SkillSection from "./sections/SkillSection";
import NavbarComponent from "./components/navbar";
import { Link, Element } from "react-scroll";
import data from "./data.json";
import SnowStorm from "react-snowstorm";

class App extends Component {
  constructor(props) {
    super(props);
    this.handleColor = this.handleColor.bind(this);
    this.state = {
      themeIndex: 0
    };
  }

  handleColor() {
    var IndexOfTheme =
      this.state.themeIndex >= parseInt(data.themes[0])
        ? 0
        : this.state.themeIndex + 1;
    console.log(IndexOfTheme);
    this.setState({ themeIndex: IndexOfTheme });
  }
  render() {
    return (
      <div className="App">
        <SnowStorm />
        <NavbarComponent onChangeColor={this.handleColor} />
        <TitleSection themeIndex={this.state.themeIndex} />
        <Element name="about" className="element" />
        <AboutSection themeIndex={this.state.themeIndex} />
        <Element name="skills" className="element" />
        <SkillSection themeIndex={this.state.themeIndex} />
      </div>
    );
  }
}

export default App;
