import React, { Component } from "react";
import data from "../data.json";
import Fullpage from "../components/Fullpage";
import "./AboutSection.css";

class AboutSection extends Component {
  render() {
    const _themeIndex = this.props.themeIndex;
    return (
      <div>
        <Fullpage
          className="second"
          color={data.themes[_themeIndex].background_color}
        >
          <h3 style={{ color: data.themes[this.props.themeIndex].font_color }}>
            {data.sections[0].title}
          </h3>
          <div
            className="paragraphs"
            style={{ color: data.themes[this.props.themeIndex].font_color }}
          >
            {data.sections[0].items.map(p => {
              return <p>{p.content}</p>;
            })}
          </div>
        </Fullpage>
      </div>
    );
  }
}

export default AboutSection;
